export function generateAuthError(message) {
  switch (message) {
    case "INVALID_PASSWORD":
      return "Email    ";

    case "INVALID_EMAIL":
      return "Email    ";

    case "EMAIL_NOT_FOUND":
      return "Email    ";

    case "EMAIL_EXISTS ":
      return "   email  ";
    default:
      return "   .  ";
  }
}
